@import url(https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap);
* {
  padding: 0;
  border: 0;
}

html {
  box-sizing: border-box;
  height: auto;
  width: auto;
  overflow-x: hidden;
}

body {
  font-family: "Space Mono", Helvetica, sans-serif;
  min-height: 100vh;
  font-size: 16px;
  background: hsl(185, 41%, 84%);
  overflow-x: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}


.text-header {
  align-items: center;
    display: flex;
    justify-content: center;
}
.card1 {
  background-color: white;
  width: 500px;
  height: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly;
  flex: 1 1;
}

.top-form{
  margin-top: 20px;
  padding: 5px;
  flex: 1 1;
}

.top-form::before{
  content: 'Bill';
  text-transform: capitalize;
  font-weight: 700;
  color: hsl(184, 14%, 56%);
  margin-bottom: 1rem;

}

.top-form input{
  background: hsl(189, 41%, 97%) url(/tip-calculaltor-app/static/media/icon-dollar.9f9be353.svg) left no-repeat;

}

.bottom-form {
  margin-bottom: 20px;
  flex: 1 1;
}


.bottom-form::before{
  content: 'Number of People';

  text-transform: capitalize;
  font-weight: 700;
  color: hsl(184, 14%, 56%);
  margin-bottom: 1rem;
}

.bottom-form input{
  background: hsl(189, 41%, 97%) url(/tip-calculaltor-app/static/media/icon-person.65bfd204.svg) left no-repeat;

}

@media (min-width: 650px) {

  .card1 {
    flex-direction: row;
    width:600px;
    /* height: 500px; */
    padding: 5px;
    justify-content: space-evenly;
  }
  
  .input-wrapper {
    width: 50%;
    justify-content: space-between;
    display: flex;
    flex-direction: column;
    align-items: center;
    
  }
  .result-wrapper{
    /* width: 50%; */
    padding: 10px;
    height: auto;
    align-items: center;
    display: flex;
    justify-content: center;
  }

}
.btn-wrapper     {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    width: 16rem;
    margin: 2.5rem 0rem;
}

.btn-header{
    text-transform: capitalize;
    font-weight: 700;
    color: hsl(184, 14%, 56%);
}


.btn-list {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
}

.button {
  width: 5rem;
  height: 0.7rem;
  padding: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn1,
.btn2,
.btn4,
.btn5 {
  margin: .5rem;
  background-color: hsl(183, 100%, 15%);
  color: hsl(0, 0%, 100%);
  font-weight: 700;
}

.btn3 {
  color: hsl(183, 100%, 15%) ;
  background-color: hsl(185, 41%, 84%);
  margin: .5em;
  font-weight: 700;
}

.btn6 {
  color: hsl(183, 100%, 15%);
  background-color: hsl(185, 41%, 84%);
  margin: .5rem;
  font-weight: 700;
} 

.card {
    border-radius: 7px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;;
}
.input-form{
    display: flex;
    flex-direction: column;
}


.input-form input{
    
    width: 15rem;
    content: '$';
    height: 1.6rem;
    color: hsl(186, 14%, 43%);
    font-weight: 700;
    text-align: right;
    padding-right: 1rem;
    border-radius: 5px;
}
.calcCard{
    background-color:  hsl(183, 100%, 15%);
    width: 15rem;
    height: 15rem;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    justify-content: space-between;
}

.calcTip,
.calcTotal {
    color: hsl(185, 41%, 84%);
    font-weight: 500;
    text-transform: capitalize;
    float: left;
}

.calcTip::after{
    content: '/ person';
    position: relative;
    left: -96px;
    top: 19px;
    font-size: .6rem;
}

.calcTotal::after{
    content: '/ person';
    position: relative;
    left: -48px;
    top: 19px;
    font-size: .6rem;
}

.calcTip span,
.calcTotal span {
    float: right;
    color:  hsl(185, 41%, 84%)  ;
    font-weight: 700;
    font-size: 1.4rem;
    letter-spacing: .2rem;
}

.resetBtn{
    text-transform: uppercase;
    background-color: hsl(185, 41%, 84%);
    color: hsl(183, 100%, 15%);
    font-weight: 700;
    width: auto;
    height: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 7px;
}

@media (min-width: 650px) {
    .calcCard {
        height: 20rem;
        margin-bottom: 0;
    }
}
